// IMPORTS:
// import axios http client
import axios from 'axios';

// Your API key: 36272835-814780967e749f1d2663a198b
// q - termin, ktry chcemy wyszuka. W tej aplikacji to tre ktr bdzie wpisywa uytkownik.
// image_type - typ obrazka. Chcemy tylko zdjcia, dlatego okrel warto parametru jako photo.
// orientation - orientacja zdjcia. Okrel warto horizontal.
// safesearch - wyszukiwanie obrazkw SFW (Safe For Work). Okrel warto true.

// fetch data
async function getPics(searchTerm, pageNumber, picsPerPage) {
  const response = await axios.get(
    `https://pixabay.com/api/?key=36272835-814780967e749f1d2663a198b&q=${searchTerm}&image_type=photo&orientation=horizontal&safesearch=true&page=${pageNumber}&per_page=${picsPerPage}`
  );
  return response;
}

// EXPORT:
export { getPics };
